.competition-popup {
    width: 1000px;
    height: 545px;
    padding: 40px 35px 40px 0;
    background-color: #fff;
    .photo-caption {
        box-sizing: border-box;
        height: 100%;
        display: flex;
        justify-content: space-between;
        &__caption {
            background-color: #f7f7f7;
            padding: 25px 35px 20px 20px;
    
        }
        &__title {
            color: map_get($gray-light, default);
            font-size: 14px;
            line-height: 20px;
        }
        &__name-user {
            color: #a9a9a9;
            font-weight: 700;
            margin-right: 5px;
        }
        &__block-info {
            // border-bottom: 1px solid #f7f7f7;
        }
        &__comments-list {
            padding: 15px 0px 0px 20px;
            height: 330px;
            overflow: auto;
            height: 245px;
            margin-bottom: 7px;
        }
        &__comments-item {
            margin-bottom: 15px;
            position: relative;
            padding-right: 15px;
            &:after {
                content: "";
                display: block;
                height: 1px;
                width: 360px;
                margin-left: -20px;
                background-color: #f7f7f7;
                position: absolute;
                bottom: 0;
            }
            .photo-caption__comments-reply-list {
                padding-left: 30px;
            }
        }
        &__comment {
            color: #2b2b2b;
            font-size: 14px;
            line-height: 20px;
        }
        &__name-user--brown {
            color: map_get($gray, default);
            font-weight: 700;
            margin-right: 5px;
        }
        &__options {
            padding-bottom: 15px;
        }
        &__options-item {
            color: map_get($gray-light, default);
            font-size: 12px;
            margin-right: 20px;
            position: relative;
            &:hover {
                color: map_get($gray-light, hover);
            }
            &:after {
                content: "";
                display: inline-block;
                height: 10px;
                width: 1px;
                background-color: #ececec;
                position: absolute;
                right: -11px;
                top: 50%;
                transform: translate(0, -50%);
            }
            &:last-child:after {
                display: none;
            }
        }
        &__options-data {
            font-style: italic;
        }
        &__new-comment {
            width: 95%;
            color: map_get($gray, default);
            height: 95px;
            resize: none;
            outline: none;
            padding: 5px;
            margin-left: 20px;
        }
    }
}


.count {
    margin-top: 15px;
    @include clearfix;
    color: map_get($blue, default);
    font-weight: 700; 
    .count__likes {
        color: map_get($blue, default);
    }
    &__comments,
    &__likes-box,
    &__total{
        display:inline-block;
        margin-right: 35px;
    }
    &__total {
        margin-right: 0;
    }
    &__comments:before {
        background-image: url(../img/competition/comments.png);
    }
    &__likes:before {
        background-image: url(../img/competition/likes.png);
    }
    &__comments:before,
    &__likes:before {
        content: "";
        display: block;
        float: left;
        width: 20px;
        height: 18px;
        padding-right: 5px;
        background-position: center center;
        background-repeat: no-repeat;
        vertical-align: middle;
    }
    &__total {
        color: #a9a9a9;
        font-size: 16px;
        font-weight: 400;
        span {
            height: 12px;
            color: map_get($gray, default);
            font-weight: 700;
        }
    }
    &__link-more {
        color: #a9a9a9;
        font-size: 12px;
        font-weight: 400;
        margin-left: 5px;
    }
}









