@import 'vars';
@import 'mixins';
@import 'parts/timer';
@import 'popups/competition-popup';



.competition__breadcrumbs {
    padding-left: 10px;
}

.competition__information {
    padding: 40px 245px 75px;
    background-color: #fff;
    p, li {
        line-height: 24px;
    }
    strong {
        font-weight: 600;
    } 
    ul li:before {
        content: '- ';
    }
}

.page-title {
    margin: 30px 0 15px 0;
    padding-left: 0;
    font-size: 34px;
}

.timer {
    margin: 15px auto;
}

.take-part {
    margin-top: 5px;
}

.photo-gallery {
    background: #fff;
    padding: 10px 20px;
    .photo-gallery__inner {
        margin: 0 -25px;
        @include clearfix;
    }
    &__item {
        margin: 10px 20px;
        float: left;
        width: 363px;
        height: 303px;
        position: relative;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        &:hover {
            .photo-gallery__item-overlay {
                opacity: 1;
                visibility: visible;
            }
        }
    }
    &__item-overlay {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, .5);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;

        opacity: 0;
        visibility: hidden;
        transition: opacity .5s,
        visibility .5s;
    }
    &__count-comments, &__count-likes {
        color: #fff;
        font-size: 24px;
        font-weight: 700;
        display: inline-block;
        line-height: 30px;
        vertical-align: middle;
        margin: 0 25px;
        &:before {
            content: '';
            display: inline-block;
            width: 30px;
            height: 30px;
            padding-right: 25px;
            vertical-align: middle;
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
    }
    &__count-comments {
        &:before {
            background-image: url('../img/icon/comment.png');
        }
    }
    &__count-likes {
        &:before {
            background-image: url('../img/icon/white-heart.png');
        }
    }
}