.timer {
    width: 200px;
    font-size: 0;
    &__value-wrap {
        border-radius: 3px;
        background-color: #f7f7f7;
        padding: 15px 0;
    }
    &__cell {
        color: #2b2b2b;
        font-size: 22px;
        font-weight: 400;
        line-height: 20px;
        &:not(:last-child) {
            border-right: 1px solid #ececec;
        }
    }
    &__cell, &__title {
        width: 25%;
        display: inline-block;
        text-align: center;
        cursor: default;
    }
    &__title {
        color: #a6a6a6;
        font-size: 12px;
        font-weight: 400;
        line-height: 12px;
        padding-top: 4px;
    }
}