.timer {
  width: 200px;
  font-size: 0; }
  .timer__value-wrap {
    border-radius: 3px;
    background-color: #f7f7f7;
    padding: 15px 0; }
  .timer__cell {
    color: #2b2b2b;
    font-size: 22px;
    font-weight: 400;
    line-height: 20px; }
    .timer__cell:not(:last-child) {
      border-right: 1px solid #ececec; }
  .timer__cell, .timer__title {
    width: 25%;
    display: inline-block;
    text-align: center;
    cursor: default; }
  .timer__title {
    color: #a6a6a6;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px;
    padding-top: 4px; }

.competition-popup {
  width: 1000px;
  height: 545px;
  padding: 40px 35px 40px 0;
  background-color: #fff; }
  .competition-popup .photo-caption {
    box-sizing: border-box;
    height: 100%;
    display: flex;
    justify-content: space-between; }
    .competition-popup .photo-caption__caption {
      background-color: #f7f7f7;
      padding: 25px 35px 20px 20px; }
    .competition-popup .photo-caption__title {
      color: #a6a6a6;
      font-size: 14px;
      line-height: 20px; }
    .competition-popup .photo-caption__name-user {
      color: #a9a9a9;
      font-weight: 700;
      margin-right: 5px; }
    .competition-popup .photo-caption__comments-list {
      padding: 15px 0px 0px 20px;
      height: 330px;
      overflow: auto;
      height: 245px;
      margin-bottom: 7px; }
    .competition-popup .photo-caption__comments-item {
      margin-bottom: 15px;
      position: relative;
      padding-right: 15px; }
      .competition-popup .photo-caption__comments-item:after {
        content: "";
        display: block;
        height: 1px;
        width: 360px;
        margin-left: -20px;
        background-color: #f7f7f7;
        position: absolute;
        bottom: 0; }
      .competition-popup .photo-caption__comments-item .photo-caption__comments-reply-list {
        padding-left: 30px; }
    .competition-popup .photo-caption__comment {
      color: #2b2b2b;
      font-size: 14px;
      line-height: 20px; }
    .competition-popup .photo-caption__name-user--brown {
      color: #2b2b2b;
      font-weight: 700;
      margin-right: 5px; }
    .competition-popup .photo-caption__options {
      padding-bottom: 15px; }
    .competition-popup .photo-caption__options-item {
      color: #a6a6a6;
      font-size: 12px;
      margin-right: 20px;
      position: relative; }
      .competition-popup .photo-caption__options-item:hover {
        color: #cbcbcb; }
      .competition-popup .photo-caption__options-item:after {
        content: "";
        display: inline-block;
        height: 10px;
        width: 1px;
        background-color: #ececec;
        position: absolute;
        right: -11px;
        top: 50%;
        transform: translate(0, -50%); }
      .competition-popup .photo-caption__options-item:last-child:after {
        display: none; }
    .competition-popup .photo-caption__options-data {
      font-style: italic; }
    .competition-popup .photo-caption__new-comment {
      width: 95%;
      color: #2b2b2b;
      height: 95px;
      resize: none;
      outline: none;
      padding: 5px;
      margin-left: 20px; }

.count {
  margin-top: 15px;
  color: #00acc1;
  font-weight: 700; }
  .count:after {
    content: "";
    display: block;
    clear: both; }
  .count .count__likes {
    color: #00acc1; }
  .count__comments, .count__likes-box, .count__total {
    display: inline-block;
    margin-right: 35px; }
  .count__total {
    margin-right: 0; }
  .count__comments:before {
    background-image: url(../img/competition/comments.png); }
  .count__likes:before {
    background-image: url(../img/competition/likes.png); }
  .count__comments:before, .count__likes:before {
    content: "";
    display: block;
    float: left;
    width: 20px;
    height: 18px;
    padding-right: 5px;
    background-position: center center;
    background-repeat: no-repeat;
    vertical-align: middle; }
  .count__total {
    color: #a9a9a9;
    font-size: 16px;
    font-weight: 400; }
    .count__total span {
      height: 12px;
      color: #2b2b2b;
      font-weight: 700; }
  .count__link-more {
    color: #a9a9a9;
    font-size: 12px;
    font-weight: 400;
    margin-left: 5px; }

.competition__breadcrumbs {
  padding-left: 10px; }

.competition__information {
  padding: 40px 245px 75px;
  background-color: #fff; }
  .competition__information p, .competition__information li {
    line-height: 24px; }
  .competition__information strong {
    font-weight: 600; }
  .competition__information ul li:before {
    content: '- '; }

.page-title {
  margin: 30px 0 15px 0;
  padding-left: 0;
  font-size: 34px; }

.timer {
  margin: 15px auto; }

.take-part {
  margin-top: 5px; }

.photo-gallery {
  background: #fff;
  padding: 10px 20px; }
  .photo-gallery .photo-gallery__inner {
    margin: 0 -25px; }
    .photo-gallery .photo-gallery__inner:after {
      content: "";
      display: block;
      clear: both; }
  .photo-gallery__item {
    margin: 10px 20px;
    float: left;
    width: 363px;
    height: 303px;
    position: relative;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; }
    .photo-gallery__item:hover .photo-gallery__item-overlay {
      opacity: 1;
      visibility: visible; }
  .photo-gallery__item-overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    opacity: 0;
    visibility: hidden;
    transition: opacity .5s, visibility .5s; }
  .photo-gallery__count-comments, .photo-gallery__count-likes {
    color: #fff;
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    line-height: 30px;
    vertical-align: middle;
    margin: 0 25px; }
    .photo-gallery__count-comments:before, .photo-gallery__count-likes:before {
      content: '';
      display: inline-block;
      width: 30px;
      height: 30px;
      padding-right: 25px;
      vertical-align: middle;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat; }
  .photo-gallery__count-comments:before {
    background-image: url("../img/icon/comment.png"); }
  .photo-gallery__count-likes:before {
    background-image: url("../img/icon/white-heart.png"); }
